<template>
<div class="contener text-center">
  <div class="row m-0 justify-content-center">
    <div class="col-12">
        <div class="alert alert-success" role="alert">
            ویرایش   پست  
        </div>
    </div>
  </div>
</div>
<div class="contener ">
  <div class="row m-0 justify-content-center">
    <div class="col-8 ">

      <form @submit.prevent="validet">

        <div class="mb-3 ">

          <label for="exampleFormControlInput1" 
          class="form-label">عنوان پست </label>

          <input type="text" v-model.lazy.trim="form.title" class="form-control" 
          id="exampleFormControlInput1" placeholder="">

          <label class="form-label text-danger">
            {{ form.errorTitle }}
          </label>

        </div>

        <div class="mb-3">
          <label for="exampleFormControlTextarea1" 
          class="form-label">محتوای پست </label>

          <textarea class="form-control" v-model.lazy.trim="form.body"
          id="exampleFormControlTextarea1" rows="6"></textarea>

          <label class="form-label  text-danger">
            {{ form.errorBody }}
          </label>
        </div>

        <button type="submit" class="btn btn-success" :disabled="loding">

          <div v-if="loding"  class="spinner-border spinner-border-sm" role="status">

          </div>
         ارسال </button>
      </form>


    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import { reactive,ref } from '@vue/reactivity'
import Swal from 'sweetalert2'

import {useRoute,useRouter}from 'vue-router'

export default {
  name :"CreatPost",
  setup(props) {

    const loding=ref(false)
    const route =useRoute()
    const router =useRouter()

    console.log(route.params)

    const form =reactive({
      title:'',
      body:'',
      errorTitle:'',
      errorBody:''
    })

          function getPost (){
            axios.get(`https://jsonplaceholder.typicode.com/posts/${route.params.id}`)
            .then(function (response) {
               // handle success
               console.log(response.data)
               form.title=response.data.title;
               form.body=response.data.body;
               loding.value=false;
            })
            .catch(function (error) {
               // handle error
              // console.log(error);
            })
      }
      getPost()



      function validet(){

          if(form.title === ""){
            form.errorTitle="عنوان نمی تواند خالی باشد "
          }else{
            form.errorTitle = '';
          }

          if(form.body === ""){
            form.errorBody="محتوا نمی تواند خالی باشد "
          }else{
            form.errorBody = '';
          }

          if(form.title != "" && form.body != ""){

            loding.value=true;
            updataPost()
          }
      
        }

      function updataPost(){
           axios.put(`https://jsonplaceholder.typicode.com/posts/${route.params.id}`,
           {
              id:route.params.id,
              title:form.title,
              body:form.body,
              userId: 1
           }
           )
            .then(function (response) {
               // handle success
               // console.log('creatPost.....')
                console.log(response.data)

                loding.value=false
               //loding.value=false;
               

                  Swal.fire({
                    title: 'Tank you',
                    text: 'success Update Post',
                    icon: 'success',
                    confirmButtonText: 'ok'
                  })

                  router.push({name:'admin-Posts'})


            })
            .catch(function (error) {
               // handle error
               console.log(error);
            })

        } 

    return{
        form,
        validet,
        loding,
        
    }
    
  }

}
</script>

<style>

</style>